.navbar {
    background-color: rgb(28, 168, 215);
    color: hsl(306, 86%, 39%);
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 1.5rem;
  }
  
  .actions button {
    background-color: rgb(18, 4, 173);
    color: #c5d20f;
    
    border: none;
    padding: 8px 16px;
    margin-left: 10px;
    cursor: pointer;
  }
  
  .actions button:hover {
    background-color: #e05712;
  }
  
  .actions div {
    display: flex;
    align-items: center;
  }
  
  .actions div button {
    margin-left: 10px;
  }